a guiarlas y mantenerlas de forma vertical, esto se realiza por medio de ciertos materiales, los cuales se amarran del tallo o de las ramas con la finalidad de darle un adecuado desarrollo, evitando así, daños durante la fase productiva.")])])]),_c('div',{staticClass:"row fila6-acord-lab-culturales-tema1",attrs:{"titulo":"Control de arvenses"}},[_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{staticClass:"imagenes-acordeon-tema1",attrs:{"src":require("@/assets/curso/temas/imagen-control-arvenses-slider1.png"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('div',{staticClass:"texto-acordeon-tema1"},[_c('p',[_vm._v("Previene, reduce y erradica plantas no deseadas, evitando interferencias de crecimiento entre las plantas, o consecuencias negativas al competir por la luz solar y los nutrientes. ")]),_c('p',[_vm._v("El mantenimiento de cultivos previene generadores de enfermedades y plagas, como también de plantas ‘hospederas’, sin desproteger el suelo. Seleccionar el método apropiado de control depende de la biología, morfología, modo de desarrollo, forma de reproducción, comportamiento, medios de difusión, etc., del arvense; e igualmente de la cantidad, las especies en cultivo, el estado de cosecha y la capacidad económica del agricultor.")]),_c('p',[_vm._v("Otras ventajas del control de arvenses son la conservación de humedad en la planta, el equilibrio, el agroecosistema, además de la regulación de presencia de insectos, pues algunos se alimentan de este. Igualmente, al implementar una variedad de control de arvenses, se actúa como repelente y controlador de insectos perjudiciales para los cultivos, controlando que se desarrollen en esa área, evitando, por ejemplo la ‘"),_c('i',[_vm._v("Ranunculus acris")]),_vm._v("’ o botón de oro, o la formación de nidos de hormigas arrieras ‘"),_c('i',[_vm._v("Atta cephalotes")]),_vm._v("’.")])])])]),_c('div',{staticClass:"row fila7-acord-lab-culturales-tema1",attrs:{"titul