ién, presentan gran adaptabilidad a la polinización cruzada y a la dispersión, tanto corta como larga, adaptándose y tolerando ambientes variables. Todas estas características le permiten una fácil y exitosa población en los agroecosistemas.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/imagen5-slider-simple-tema1.png"),"alt":"Texto que describa la imagen"}})])])])])],1)]),_c('br'),_c('br'),_c('br'),_vm._m(5),_c('br'),_c('br'),_c('br'),_vm._m(6),_c('br'),_c('br'),_c('br'),_vm._m(7),_c('br'),_c('br'),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("A continuación, se detallan algunas diferencias y particularidades de tales elementos:")]),_c('div',{staticClass:"row container justify-content-around align-items-center"},[_c('div',{staticClass:"row"},[_vm._m(8),_c('SlyderB',{staticClass:"mb-5 slider2-tema1",attrs:{"datos":_vm.datosSlyder2}})],1)])])],1)}
