n rol ecológico en la interacción con el cultivo y, a su vez, son muy útiles para controlar plagas y enfermedades. A continuación, se describe la utilidad de las arvenses.")]),_c('div',{staticClass:"row color-columna-izquierda-tarjeta-utilidad",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"col-lg-7"},[_c('div',{staticClass:"bloque-texto-tarjeta-utilidad-tema1 p-2"},[_c('div',{staticClass:"col-sm mb-5 mb-sm-0"},[_c('ol',{staticClass:"lista-ol--cuadro"},[_c('li',[_c('div',{staticClass:"lista-ol--cuadro__vineta color-acento-contenido"},[_c('span',[_vm._v("b")])]),_vm._v("Utilidad ")])])]),_c('p',{staticClass:"texto-inf-tarjeta-utilidad-tema1 pe-4"},[_vm._v("Las arvenses no solo aportan de forma negativa, también, como lo indica Aguilar et al., (2021), generan impactos positivos al ser un factor inherente a la diversidad de los ecosistemas, contribuyendo con la conservación del suelo, aumentando la estabilidad de los agroecosistemas, incrementando la cantidad de material genético y ayudando a recuperar la biodiversidad vegetal. Todo esto, brinda las condiciones necesarias para que existan mayor diversificación de la fauna entomológica, es decir, promueve la reproducción de insectos y artrópodos. Además, las arvenses pueden ser fuente de alimento o servir como materia prima en la fabricación de fertilizantes orgánicos, incluso, algunos cuentan con propiedades medicinales.")])])]),_c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{staticClass:"imagen-tarjeta-utilidad-tema1",attrs:{"src":require("@/assets/curso/temas/imagen-derecha-tarjeta-utilidad.png"),"alt":"Texto que describa la imagen"}})])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-around align-items-center"},[_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("En el siguiente esquema, analice otras particularidades del rol ecológico y la utilidad de las arvenses:")]),_c('div',{staticClass:"row